body {
	position: relative;
	min-width: 320px;
	background-color: #e6e6e6;
	font-family: "helveticaneuecyr-medium-webfont", sans-serif;
	font-size: 16px;
	overflow-x: hidden; }

.body-wrapper {
	position: relative;
	max-width: 1200px;
	min-width: 320px;
	margin: 0 auto;
	background-color: #FFF;
	box-shadow: 0 0 15px rgba(0,0,1,0.75); }

.hidden {
	display: none; }
